import styled from "styled-components";
import Layout from "../components/Layout";
import { SubMenu } from "../components/SubMenu";
import { NAVIGATION } from "../data/navigation";

const StyledContainer = styled.div`
  display: flex;
`;
const StyledContent = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
`;
const StyledTitle = styled.h2`
  text-align: center;
  font-style: italic;
  max-width: 70%;
  margin: 0 auto;
  padding-bottom: 30px;
`;

const StyledDescription = styled.div``;
const StyledParagraph = styled.p`
  padding: 15px 0;
`;
const StyledDecimalList = styled.ul`
  list-style: decimal;
  padding-left: 20px;
  padding-bottom: 15px;
`;
const StyledList = styled.ul`
  list-style: inside;
  padding-bottom: 15px;
`;

const ONas = () => {
  return (
    <Layout>
      <StyledContainer>
        <SubMenu submenuItems={NAVIGATION[0].subpages} />
        <StyledContent>
          <StyledTitle>
            Szanowni Pastwo, Drogie Koleanki i Drodzy Koledzy!
          </StyledTitle>
          <StyledDescription>
            <StyledParagraph>
              Na stronie ZNPnU bdziemy zamieszczali materiay dotyczce
              nauczania jzyka i kultury polskiej w systemie owiaty
              ukraiskiej.
            </StyledParagraph>
            <StyledParagraph>
              Na podstronie{" "}
              <strong>
                <i>O nas</i>
              </strong>{" "}
              poznacie Pastwo histori zaoenia Zjednoczenia Nauczycieli
              Polskich na Ukrainie, zadania statutowe organizacji, zasady
              powoania Oglnoukraiskiego Koordynacyjno-Metodycznego Centrum
              Nauczania Jzyka i Kultury Polskiej w Drohobyczu, cele i
              dziaalno Centrum, histori adaptacji budynku bdcego siedzib
              Centrum przy ul. Truskawieckiej 9 w Drohobyczu w obwodzie lwowskim
              oraz uzasadnienie nauczania jzyka i wiedzy o kulturze polskiej w
              wietle prawa ukraiskiego.
            </StyledParagraph>
            <StyledParagraph>
              Na podstronie{" "}
              <strong>
                <i>Materiay</i>
              </strong>
              , w dziale zatytuowanym Programy, znajdziecie Pastwo programy
              nauczania jzyka polskiego i przedmiotw nauczanych w jzyku
              polskim w rnych typach szk oglnoksztaccych funkcjonujcych
              na terytorium Ukrainy:
            </StyledParagraph>
            <StyledDecimalList>
              <li>
                Programy nauczania w klasach z polskim jzykiem nauczania: Jzyk
                polski dla klas 1-4, 5-9, 10-11; Czytanie kl. 1-4; Kurs
                integrowany (literatura polska i powszechna); Programy nauczania
                geografii Polski, historii Polski, literatury polskiej i wiedzy
                o narodzie polskim.
              </li>
              <li>
                Program nauczania jzyka polskiego jako ojczystego dla klas 1-4,
                5-9 (kontynuacja) oraz 5-9 (pocztek nauki w klasie 5).
              </li>
              <li>
                Program nauczania jzyka polskiego jako drugiego obcego dla klas
                5-9. Program wedug koncepcji Nowej Szkoy Ukraiskiej. Dla
                klasy 5 rozpoczynajcej nauczanie jzyka polskiego w roku
                szkolnym 2022/23 opracowano podrcznik dla klasy 5, rozkad
                materiau i konspekty lekcji.
              </li>
              <li>
                Program zaj fakultatywnych z jzyka polskiego dla klas 1-4,
                5-9, 10-11 wraz z przykadowymi konspektami. Program jzyka
                polskiego jako fakultatywnego dla kl. 5-11.
              </li>
              <li>
                Programy dla pozaszkolnych placwek owiatowych:
                owiatowo-kulturalnych, centrw polskiej mniejszoci narodowej,
                polskich sobotnio-niedzielnych szk z przedmiotw: jzyk
                polski, literatura polska, geografia Polski, historia Polski,
                polska kultura muzyczna, wiedza o narodzie polskim.
              </li>
            </StyledDecimalList>
            <StyledParagraph>
              Ponadto podstrona{" "}
              <strong>
                <i>Materiay</i>
              </strong>{" "}
              bdzie zawieraa nastpujce dziay:
            </StyledParagraph>
            <StyledParagraph>
              <i>Konspekty</i> zawierajce konspekty i scenariusze lekcji,
              zaj, imprez, uroczystoci, wycieczek szkolnych, a take
              propozycje wicze do wykorzystania w pracy dydaktycznej.
            </StyledParagraph>
            <StyledParagraph>
              <i>Kursy</i> zawierajce wykady, wiczenia oraz prezentacje z
              kursw i warsztatw doskonalenia zawodowego nauczycieli jzyka
              polskiego.
            </StyledParagraph>
            <StyledParagraph>
              <i>Podrczniki</i> zawierajce informacje o podrcznikach
              dopuszczonych do uytku szkolnego.
            </StyledParagraph>
            <StyledParagraph>
              <i>Olimpiady</i> zawierajce informacje o Oglnoukraiskich
              Olimpiadach Jzyka Polskiego i Literatury Polskiej, arkusze zada
              z lat poprzednich i klucze do ich rozwizania.
            </StyledParagraph>
            <StyledParagraph>
              Na podstronie{" "}
              <strong>
                <i>Dokumenty</i>
              </strong>{" "}
              znajdziecie Pastwo kompendium wiedzy z dziedziny prawa
              owiatowego i organizacji szkolnictwa. Dowiecie si Pastwo m.in.
              jak rejestrowa rne typy placwek owiatowych, na jakich
              zasadach wprowadza jzyk polski do nauczania, jakie uniwersytety
              ksztac polonistw. Poznacie adresy szk i placwek
              przedszkolnych.
            </StyledParagraph>
            <StyledParagraph>
              Informacje te zostay ujte w nastpujcych dziaach:
            </StyledParagraph>
            <StyledList>
              <li>
                <i>Klasy z polskim jzykiem nauczania;</i>
              </li>
              <li>
                <i>Filologia polska na Ukrainie;</i>
              </li>
              <li>
                <i>Nauczanie jzyka polskiego jako obcego;</i>
              </li>
              <li>
                <i>Pozaszkolne placwki owiatowe;</i>
              </li>
              <li>
                <i>Sobotnio-niedzielne szkoy;</i>
              </li>
              <li>
                <i>Przedszkola i grupy przedszkolne.</i>
              </li>
            </StyledList>
            <StyledParagraph>
              Na podstronie{" "}
              <strong>
                <i>Kontakt</i>
              </strong>{" "}
              zamiecilimy numery telefonw i adresy poczty elektronicznej
              Zjednoczenia Nauczycieli Polskich na Ukrainie i Oglnoukraiskiego
              Koordynacyjno-Metodycznego Centrum Nauczania Jzyka i Kultury
              Polskiej w Drohobyczu
            </StyledParagraph>
          </StyledDescription>
        </StyledContent>
      </StyledContainer>
    </Layout>
  );
};

export default ONas;
