import styled from "styled-components/macro";
import FileLink from "../components/FileLink";
import Layout from "../components/Layout";
import { SubMenu } from "../components/SubMenu";
import { NAVIGATION } from "../data/navigation";

const StyledContainer = styled.div`
  display: flex;
`;
const StyledContent = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
`;

const UkrainianSchool = () => {
  return (
    <Layout>
      <StyledContainer>
        <SubMenu submenuItems={NAVIGATION[4].subpages} />
        <StyledContent>
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/Pol.5-9-kl.pdf"
            title="    . 5-9  (    1 )      (.  ..,  ..)"
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/PolMatskovych.pdf"
            title="    . 5-9  (   5- )      (.  ..,  -,  ..)"
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/dlya-zagalnoosvitnix-navchalnix-zakladiv.pdf"
            title="  10 11             "
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/Polmowawojcewa1-2kl.doc"
            title="         1-2     "
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/3-4..doc"
            title="      .         3-4         "
          />
        </StyledContent>
      </StyledContainer>
    </Layout>
  );
};

export default UkrainianSchool;
