import styled from "styled-components/macro";
import Layout from "../components/Layout";
import { SubMenu } from "../components/SubMenu";
import { NAVIGATION } from "../data/navigation";
import FileLink from "../components/FileLink";

const StyledContainer = styled.div`
  display: flex;
`;
const StyledContent = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
`;

const PolishLanguageClasses = () => {
  return (
    <Layout>
      <StyledContainer>
        <SubMenu submenuItems={NAVIGATION[4].subpages} />
        <StyledContent>
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/Geografia.pdf"
            title="    .   5-6 , 10 -11   /   . :  .."
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/HistoriaPolski.pdf"
            title="A        /   5-9, 10-11 "
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/Jezyk_polski.pdf"
            title="    .   5-11 .   /  / : . . ."
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/Literatura_Polski.pdf"
            title="    .   7-11 .   /  / :   "
          />
          <FileLink
            isFileDownload={false}
            fileLink="/znpu-react/files/Narodoznawstwo.pdf"
            title="       10  11   /  .  : .. ., .  . .,  ..,  .."
          />
        </StyledContent>
      </StyledContainer>
    </Layout>
  );
};

export default PolishLanguageClasses;
