import styled from "styled-components";
import logo from "../assets/images/logo.png";
import { Link } from "react-router-dom";
import { NAVIGATION } from "../data/navigation";
import facebookIcon from "../assets/images/facebook-60.png";

const StyledFooterContainer = styled.div`
  margin-top: auto;
  background: var(--secondary);
  width: 100vw;
  color: #fff;
  @media (max-width: 700px) {
    display: none;
  }
  .facebookLink {
    margin-left: -5px;
    img {
      margin-top: 15px;
    }
  }
`;
const StyledFooterContent = styled.div`
  max-width: 1640px;
  padding: 30px 30px 10px 30px;
  margin: 0 auto;
`;
const StyledFooterTitle = styled(Link)`
  display: flex;
  align-items: center;
  padding-bottom: 40px;
  text-decoration: none;
  color: var(--white);
`;
const StyledLogo = styled.img`
  height: 80px;
  width: auto;
  margin-right: 15px;
`;
const StyledTitle = styled.h2`
  font-size: 20px;
  font-weight: 500;
`;
const StyledFooterMainContainer = styled.div`
  display: flex;
`;
const StyledContactContainer = styled.div`
  display: flex;
  flex-direction: column;
  font-size: 16px;
  line-height: 40px;
  @media (max-width: 1075px) {
    font-size: 14px;
    line-height: 30px;
  }
`;
const StyledAddress = styled.p`
  color: var(--light-text);
  line-height: 25px;
  padding-bottom: 10px;
  @media (max-width: 1075px) {
    max-width: 300px;
    line-height: 18px;
    padding-bottom: 10px;
  }
`;
const StyledLink = styled(Link)`
  text-decoration: none;
  color: var(--light-text);
  transition: color 0.3s;
  &:hover {
    color: var(--white);
  }
`;
const StyledFooterPageMap = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 200px;
  margin-left: 300px;
  @media (max-width: 1330px) {
    margin-left: 150px;
    column-gap: 100px;
  }
  @media (max-width: 1075px) {
    margin-left: 70px;
    column-gap: 70px;
  }
`;
const StyledPageMapLink = styled(Link)`
  text-decoration: none;
  color: var(--light-text);
  text-transform: capitalize;
  font-size: 15px;
  line-height: 35px;
  transition: color 0.3s;
  &:hover {
    color: var(--white);
  }
`;
const StyledBottomText = styled.p`
  font-size: 12px;
  text-align: center;
  margin-top: 100px;
  color: var(--secondary-light-text);
`;
const Footer = () => (
  <StyledFooterContainer>
    <StyledFooterContent>
      <StyledFooterTitle to="/">
        <StyledLogo
          src={logo}
          alt="Zjednoczenie nauczycieli polskich w Ukrainie"
        />
        <StyledTitle>
          Zjednoczenie nauczycieli <br />
          polskich w Ukrainie
        </StyledTitle>
      </StyledFooterTitle>
      <StyledFooterMainContainer>
        <StyledContactContainer>
          <StyledAddress>
            82100 Drohobycz ul. Truskawiecka 9 obwd lwowski
          </StyledAddress>
          <StyledAddress>
             <br />
            82100  <br />
            .  , 21 /  17
          </StyledAddress>
          <StyledLink to="tel:0324450177">03244 50177</StyledLink>
          <StyledLink to="mailto:adam-ch@mail.lviv.ua">
            adam-ch@mail.lviv.ua
          </StyledLink>
          <StyledLink to="mailto:znpu.ua@gmail.com">
            znpu.ua@gmail.com
          </StyledLink>
        </StyledContactContainer>
        <StyledFooterPageMap>
          {NAVIGATION.map((item) => (
            <StyledPageMapLink to={item.link} key={item.title}>
              {item.title}
            </StyledPageMapLink>
          ))}
        </StyledFooterPageMap>
      </StyledFooterMainContainer>
      <StyledLink
        to="https://www.facebook.com/CentrumDrohobycz"
        className="facebookLink"
      >
        <img src={facebookIcon} alt="Facebook Icon" height={40} width={40} />
      </StyledLink>
      <StyledBottomText>
        2023  Zjednoczenie nauczycieli polskich w Ukraine
      </StyledBottomText>
    </StyledFooterContent>
  </StyledFooterContainer>
);
export default Footer;
