import styled from "styled-components";
import Layout from "../components/Layout";
import { SubMenu } from "../components/SubMenu";
import { NAVIGATION } from "../data/navigation";
import testImage from "../assets/images/test-image.png";
import nataliaTulasiewiczImage from "../assets/images/natalia-tulasiewicz.png";
import { Link } from "react-router-dom";
import { POSTS } from "../data/newsPosts";
import { NewPost } from "../components/NewPost";

const StyledContent = styled.div`
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  @media (max-width: 700px) {
    display: flex;
    flex-direction: column;
  }
`;

const StyledAboutUsContainer = styled.div`
  border: 1px solid var(--grey);
  padding: 20px;
  min-height: 800px;
  @media (max-width: 700px) {
    min-height: auto;
  }
`;

const StyledSectionTitle = styled.h4`
  text-align: center;
  font-size: 20px;
  text-transform: uppercase;
  @media (max-width: 700px) {
    padding-bottom: 10px;
    font-size: 18px;
  }
`;

const StyledGirlContainer = styled.div`
  border: 1px solid var(--grey);
  padding: 20px 15px;
  min-height: 800px;
  @media (max-width: 700px) {
    min-height: auto;
  }
`;

const StyledNewsContainer = styled.div`
  border: 1px solid var(--grey);
  padding: 20px 15px;
  min-height: 800px;
`;

const StyledAboutUsImage = styled.img`
  height: auto;
  width: 100%;
  padding: 20px 30px;
  @media (max-width: 700px) {
    height: 100px;
    width: 100px;
    padding: 0px;
    padding-top: 5px;
    padding-right: 10px;
  }
`;

const StyledDescription = styled.p`
  font-size: 14px;
  text-align: justify;
  @media (max-width: 700px) {
    max-height: 137px;
    overflow: hidden;
    font-size: 12px;
  }
`;
const StyledMoreLink = styled(Link)`
  color: var(--black);
  text-align: center;
  display: block;
  width: 100%;
  font-weight: 700;
  margin-top: 20px;
`;

const StyledGirlTitle = styled.h4`
  font-style: italic;
  text-align: center;
  font-size: 18px;
  line-height: 25px;
  font-weight: 900;
  @media (max-width: 700px) {
    font-size: 14px;
    line-height: 20px;
  }
`;
const StyledImageContainer = styled.div`
  @media (max-width: 700px) {
    margin: 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
  }
`;
const StyledGirlImage = styled.img`
  height: auto;
  width: 100%;
  padding: 20px 40px;
  @media (max-width: 700px) {
    max-height: 200px;
    width: auto;
    padding: 20px 0;
    text-align: center;
  }
`;
const StyledSectionContent = styled.div`
  @media (max-width: 700px) {
    display: flex;
  }
`;

const Home = () => {
  return (
    <Layout>
      <StyledContent>
        <SubMenu submenuItems={NAVIGATION[0].subpages} />
        <StyledAboutUsContainer>
          <StyledSectionTitle>o nas</StyledSectionTitle>
          <StyledSectionContent>
            <StyledAboutUsImage src={testImage} alt="O nas" />
            <StyledDescription>
              Zrzeszamy nauczycieli jzyka polskiego i przedmiotw nauczanych po
              polsku w przedszkolach, szkoach rednich, wyszych i punktach
              pozaszkolnych. Popularyzujemy wiedz o polskiej kulturze i
              historii. Pomagamy podnosi kwalifikacje nauczycieli, tworzy
              programy nauczania. Opracowujemy i zaopatrujemy szkoy w
              podrczniki i pomoce szkolne. Udzielamy pomocy metodycznej i
              dydaktycznej swoim czonkom, przeprowadzamy konferencje
              naukowo-praktyczne, seminaria, szkolenia i stae, a take obozy i
              wczasy, festiwale, konkursy, olimpiady przedmiotowe, kongresy i
              zjazdy z udziaem ekspertw z kraju i zagranicy. Wysyamy take
              dzieci, modzie i osoby dorose na stae w wyszych,
              specjalistycznych i rednich placwkach owiatowych, wspieramy
              rwnie inne formy nauki na Ukrainie i poza jej granicami.
              Publikujemy wybitne prace zagranicznych i miejscowych ekspertw w
              zakresie owiaty, okazujemy materialn i metodyczn pomoc
              placwkom owiatowym. Prowadzimy wymian dowiadcze midzy
              nauczycielami z Polski, Ukrainy i innych pastw.
            </StyledDescription>
          </StyledSectionContent>
          <StyledMoreLink to="/o-nas">wicej {`  >`}</StyledMoreLink>
        </StyledAboutUsContainer>
        <StyledGirlContainer>
          <StyledGirlTitle>
            Wybra mnie pan do cudownej misji mioci w wiecie dla ktrej
            zycie odda warto, a c dopiero talent i wszystkie siy
          </StyledGirlTitle>
          <StyledImageContainer>
            <StyledGirlImage
              src={nataliaTulasiewiczImage}
              alt="Natalia Tulasiewicz"
            />
          </StyledImageContainer>
          <StyledDescription>
            Natalia Tuasiewicz urodzia si 9 kwietnia 1906 roku w Rzeszowie.
            Jej rodzina pochodzia z Krakowa. Od 1913 roku uczszczaa do
            Szkoy Podstawowej w Ktach, a od 1917 roku bya uczennic
            Prywatnego Gimnazjum eskiego w Krakowie. W 1921 roku przeniosa
            si wraz z rodzin do Poznania. Tam rozpocza nauk w
            GimnazjumSistr Urszulanek Unii Rzymskiej. Ukoczya je w 1926
            roku. Po maturze studiowaa polonistyk i muzykologi na
            Uniwersytecie Poznaskim. Studia ukoczya w 1931 roku. W 1932 roku
            obronia prac ,,Mickiewicz a muzyka i uzyskaa tytu magistra
            filologii polskiej. Na wszystkich etapach edukacyjnych daa si
            pozna jako ambitna, zdolna uczennica....
          </StyledDescription>
          <StyledMoreLink to="/o-nas/natalia-tulasiewicz">
            wicej {`  >`}
          </StyledMoreLink>
        </StyledGirlContainer>
        <StyledNewsContainer>
          <StyledSectionTitle>aktualnoci</StyledSectionTitle>
          <div>
            {POSTS.map((post, i) => (
              <NewPost post={post} key={i} />
            ))}
          </div>
          <StyledMoreLink to="/o-nas/aktualnosci">
            wszystkie aktualnoci {`  >`}
          </StyledMoreLink>
        </StyledNewsContainer>
      </StyledContent>
    </Layout>
  );
};
export default Home;
