import styled from "styled-components/macro";
import Layout from "../components/Layout";
import { SubMenu } from "../components/SubMenu";
import { NAVIGATION } from "../data/navigation";
import nataliaTulasiewiczImage from "../assets/images/natalia-tulasiewicz.png";

const StyledContainer = styled.div`
  display: flex;
`;
const StyledContent = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
`;
const StyledTitle = styled.h2`
  text-align: center;
  font-style: italic;
  max-width: 70%;
  margin: 0 auto;
  padding-bottom: 30px;
`;
const StyledImage = styled.img`
  max-width: 300px;
`;
const StyledDescription = styled.p`
  text-align: justify;
  font-size: 15px;
  line-height: 20px;
  padding-top: 30px;
`;

const NataliaTulasiewicz = () => {
  return (
    <Layout>
      <StyledContainer>
        <SubMenu submenuItems={NAVIGATION[0].subpages} />
        <StyledContent>
          <StyledTitle>
            Wybra mnie pan do cudownej misji mioci w wiecie dla ktrej
            zycie odda warto, a c dopiero talent i wszystkie siy
          </StyledTitle>
          <StyledImage
            src={nataliaTulasiewiczImage}
            alt="Natalia Tulaiewicz"
          />
          <StyledDescription>
            <strong>Natalia Tuasiewicz</strong> urodzia si 9 kwietnia 1906
            roku w Rzeszowie. Jej rodzina pochodzia z Krakowa. Od 1913 roku
            uczszczaa do Szkoy Podstawowej w Ktach, a od 1917 roku bya
            uczennic Prywatnego Gimnazjum eskiego w Krakowie. W 1921 roku
            przeniosa si wraz z rodzin do Poznania. Tam rozpocza nauk w
            Gimnazjum Sistr Urszulanek Unii Rzymskiej. Ukoczya je w 1926
            roku. Po maturze studiowaa polonistyk i muzykologi na
            Uniwersytecie Poznaskim. Studia ukoczya w 1931 roku. W 1932 roku
            obronia prac ,,Mickiewicz a muzyka i uzyskaa tytu magistra
            filologii polskiej. Na wszystkich etapach edukacyjnych daa si
            pozna jako ambitna, zdolna uczennica. Fascynowaa si literatur,
            filozofi, teologi, muzyk i teatrem. ywo angaowaa si w ycie
            kulturalne poprzez uczestnictwo w koncertach i wystawach. Pisaa
            wiersze i prowadzia dziennik. Rwnolegle pogbiaa swoje ycie
            duchowe. Braa udzia w dniach skupienia i rekolekcjach.
            Interesowaa si ide apostolstwa ludzi wieckich.
            <br /> Na temat swych penych pasji de i wyboru drogi yciowej
            napisaa:
            <i>
              ,,Chc wysugiwa sobie wito na ziemi codziennym wysikiem
              dnia. Nie oddzielam szarego ycia od ideaw, ku ktrym podam.
              Wszystko: praca, nauka, sen, przyjemno, wszystko wcigam w swj
              program doskonalenia si.
            </i>
            W latach 19311937 bya nauczycielk szk poznaskich - prywatnej
            Szkoy koedukacyjnej pw. w. Kazimierza i Gimnazjum Sistr
            Urszulanek Unii Rzymskiej.
            <br /> W 1938 roku udaa si w podr do Woch i wzia udzia w
            uroczystociach kanonizacyjnych Andrzeja Boboli. <br />
            Planowaa doktorat o Karolu Hubercie Rostworowskim. Jej zamiary
            pokrzyowa wybuch II wojny wiatowej.
            <br /> Po wysiedleniu wraz z ca rodzin z Poznania w 1939 roku
            zamieszkaa u krewnych w Krakowie. Opiekowaa si rodzicami i
            wspieraa materialnie bliszych oraz dalszych krewnych. Mimo
            grocych ze strony okupanta powanych konsekwencji uczestniczya w
            tajnym nauczaniu dzieci. Organizowaa spotkania, na ktrych
            dyskutowano o literaturze. Uczya swych podopiecznych pieni
            religijnych i patriotycznych. Prowadzia adoracje wielkopostne i
            wieczorki poezji. Braa te udzia w tak zwanych objazdach
            edukacyjnych, podczas ktrych wygaszaa referaty, organizowaa
            rekolekcje duchowe i czuwaa nad wszechstronnym rozwojem swych
            uczennic zgodnie z przyjtym przesaniem:
            <br />
            <i>
              ,,Chc [] rozbudzi w nich zamiowanie do wiedzy, dopomc [], by
              ich wojna nie wyjaowia z poczucia pikna i dobra.
            </i>
            <br />
            Dziaaa w ruchu oporu. Z inicjatywy metropolity krakowskiego Adama
            Sapiehy zaangaowaa si w prac misyjn wrd robotnikw
            przymusowych. W 1943 roku wyjechaa do Hanoweru jako tajny
            penomocnik  emisariusz Rzdu Londyskiego i wiecki aposto w
            ramach Wydziau Duszpasterskiego konspiracyjnej organizacji
            Zachd. Pracowaa niewolniczo w fabryce i jednoczenie penia
            posug religijno - spoeczno-owiatow wrd robotnikw. Swoje
            zadanie traktowaa jako yciowe powoanie. 14 sierpnia 1943 roku,
            jeszcze przed wyjazdem do III Rzeszy, Zanotowaa w dzienniku:{" "}
            <i>
              Mam odwag by wit () tylko wito jest najpeniejsz
              mioci, wic nie tylko chc, ale musz by wit, nowoczesna
              wit, teocentryczn humanistk! To jest meta, do ktrej jawnie i
              odwanie si przyznaj. S dzi ludzie, ktrzy za wszelk cen
              chc by dyktatorami, s inni, co chc by multimilionerami,
              krlami nafty, smalcu, ksitami prasy, radia, filmu i telewizji.
              I nikt im si nie dziwi. Ja chc o wiele mniej i o wiele
              jednoczenie wicej. I niech si dziwi, kto chce
            </i>
            <br /> Po omiu miesicach wytonej pracy, na wiosn 1944 roku,
            zostaa zdekonspirowana. przez nieostronego kuriera z Polski. Przez
            p roku bya wiziona w Hanowerze i w Kolonii. Przesza brutalne
            ledztwo. Nastpnie z wyrokiem mierci zostaa wysana do
            niemieckiego obozu koncentracyjnego w Ravensbrck (numer 75188).
            Wycieczona cik fizyczn prac, grulic, godem, nie ustawaa w
            misji edukacyjnej i ewangelizacyjnej. Gosia katechezy, nauczaa,
            przygotowywaa mode dziewczyny do tak zwanej maej matury. Z pasj
            wykadaa polsk literatur i histori. Organizowaa wieczory
            poetyckie. Prowadzia naboestwa.
            <br /> W Wielki Pitek 30 marca 1945 roku, podczas ostatniej
            obozowej selekcji, zostaa zakwalifikowana do stracenia. 31 marca
            1945 roku zgina w komorze gazowej. Jej spopielone w krematorium
            prochy spoczy najprawdopodobniej w obozowym stawie.
            <br /> 13 czerwca 1999 roku w Warszawie papie Jan Pawe II
            beatyfikowa Natali Tuasiewicz wraz ze 108 mczennikami II wojny
            wiatowej. Ich wito patronalne przypada 12 czerwca.
            <br /> Przemylenia, listy i zapiski Natalii Tuasiewicz wydano
            pomiertnie w kilku tomach: ,,Droga do mioci, ,, Przeciw
            barbarzystwu  listy, dzienniki, wspomnienia, ,, Poprzez ziemi
            ukochaam niebo, ,,By poetk ycia. Zapiski z lat 1938-1943.
            <br />
            Posta bogosawionej przyblia praca Jacka Hadrysia i Barbary
            Judkowiak ,,Wierno asce i Sowu. Bogosawiona Natalia
            Tuasiewicz.
          </StyledDescription>
        </StyledContent>
      </StyledContainer>
    </Layout>
  );
};
export default NataliaTulasiewicz;
